import axios from "axios";

export const sendDataApi = async (values) => {
    try {
        const response = await axios.post(`https://fzz6xp4iokmhryxw5rdtw3c4oy0gyrlz.lambda-url.us-west-2.on.aws/`, values,
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            }
        );

        return response;
    } catch (error) {
        throw error;
    }

}